/* eslint-disable */
import ng from 'vendor-bb-angular';

import bbStorageModuleKey, { bbStorageServiceKey } from 'lib-bb-storage-ng';
import accessGroupsData from './data-bb-accessgroups-http';

const accessGroupsDataModuleKey = 'data-bb-accessgroups-http-ng';

export const accessGroupsDataKey = 'data-bb-accessgroups-http-ng:accessGroupsData';

export default ng
  .module(accessGroupsDataModuleKey, [
    bbStorageModuleKey,
  ])
  .provider(accessGroupsDataKey, [() => {
    const config = {
      baseUri: '/',
    };

    return {
      setBaseUri: (baseUri) => {
        config.baseUri = baseUri;
      },
      $get: [
        '$q',
        bbStorageServiceKey,
        '$httpParamSerializer',
        /* into */
       accessGroupsData(config),
      ],
    };
  }])
  .name;



